<template>
  <div class="settlement-management">
    <template v-if="$route.matched.length <= 2">
      <el-new-card>
        <template slot="header">{{ $route.meta.name }}</template>

        <el-form inline size="small" :model="searchForm" ref="searchForm">
          <el-form-item prop="stmtName">
            <el-input v-model="searchForm.stmtName" placeholder="请输入结算期数"></el-input>
          </el-form-item>
          <el-form-item prop="stmtSts">
            <el-select v-model="searchForm.stmtSts" placeholder="请选择结算状态">
              <el-option label="全部" :value="1"></el-option>
              <el-option label="已结算" :value="2"></el-option>
              <el-option label="部分已结算" :value="3"></el-option>
              <el-option label="结算中" :value="4"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="stmtMan">
            <el-input v-model="searchForm.stmtMan" placeholder="请输入收款确认人"></el-input>
          </el-form-item>
          <el-form-item prop="stmtDate">
            <el-new-date-picker v-model="searchForm.stmtDate"></el-new-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button plain type="primary" icon="el-icon-search" @click="onSearch">查询</el-button>
            <el-button plain icon="el-icon-refresh-right" @click="onReset">重置</el-button>
          </el-form-item>
        </el-form>
      </el-new-card>

      <el-new-card>
        <template slot="header">结算列表</template>

        <el-new-table :apiName="apiName" ref="table">
          <el-table-column prop="stmtName" label="结算期数" align="center">
            <template slot-scope="scope">
              <el-link type="primary" @click="toDetail(scope.row.id)">{{ scope.row.stmtName }}</el-link>
            </template>
          </el-table-column>
          <el-table-column prop="stmtType" label="结算类型" align="center"></el-table-column>
          <el-table-column prop="stmtSts" label="结算状态" align="center">
            <template slot-scope="scope">
              <stmt-sts-icon :status="scope.row.stmtSts" />
            </template>
          </el-table-column>
          <el-table-column prop="stmtMan" label="收款确认人" align="center"></el-table-column>
          <el-table-column prop="stmtDate" label="收款确认时间" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.stmtDate | fmtDateTime }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label="结算生成时间" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.createTime | fmtDateTime }}</span>
            </template>
          </el-table-column>
        </el-new-table>
      </el-new-card>
    </template>

    <router-view v-else />
  </div>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'
import ElNewTable from '@/components/ElNewTable'
import ElNewDatePicker from '@/components/ElNewDatePicker'
import StmtStsIcon from '@/components/status/StmtStsIcon'

export default {
  name: 'settlement-management',
  components: {
    ElNewCard,
    ElNewTable,
    ElNewDatePicker,
    StmtStsIcon
  },
  data () {
    return {
      apiName: 'serviceGetStmtList',
      searchForm: { // 搜索筛选条件
        stmtName: '',
        stmtSts: '',
        stmtMan: '',
        stmtDate: []
      }
    }
  },
  methods: {
    onSearch () {
      this.$refs.table.reset()
    },

    onReset () {
      this.$refs.searchForm.resetFields()
    },

    toDetail (id) {
      this.$router.push({
        path: '/settlementMgmt/settlementDetail',
        query: { id: id }
      })
    }
  }
}
</script>

